* {
  box-sizing: border-box;
}

body {
  --active-link-color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  display: block;
}

.menu__list {
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.menu__list > li {
  margin: 0 10px;
}

.menu__list__link {
  color: #000;
  justify-content: center;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.menu__list__link.is-active {
  color: var(--active-link-color);
  position: relative;
}

.menu__list__link:hover {
  color: var(--active-link-color);
}

.menu__list li:first-child {
  margin-left: 0;
}

.menu__list li:last-child {
  margin-right: 0;
}

.is-active:after {
  content: "";
  background-color: var(--active-link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

